* {
	margin: 0;
	padding-bottom: 10px;
	box-sizing: border-box;
}
body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	background-color: whitesmoke;
	color: black;
}
.booklist {
	width: 90vw;
	max-width: 1170px;
	margin: 5rme auto;
	display: grid;
	gap: 2rem;
}
@media screen and (min-width: 768px) {
	.booklist {
		grid-template-columns: repeat(3, 1fr);
		/* align-items: start; */
	}
}

.book {
	background: #fff;
	border-radius: 1rem;
	padding: 1rem 2rem;
}
.book p {
	margin-top: 0.5rem;
}
.book h1 {
	margin-top: 0.5rem;
}
.book h4 {
	color: #617d98;
	font-size: 0.75rem;
	letter-spacing: 5px;
	margin-top: 0.25rem;
}
